:root{
  --main-bg-color:#743eab;
}
html{
    width: 100%;
    margin: 0px;
    padding: 0px;
}
body {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #e9ecef;
/* So the width will be 100% + 17px */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; 
}

.btn-custom{
  border-color: #743eab;
  border-color: var(--main-bg-color);
  background-color: #743eab;;
  color:#fff;
}
.btn-custom:focus{
  box-shadow: none;
}

input[type="text"], input[type="email"], input[type='password'],textarea[type='text']{
  background-color : #d1d1d1; 

}

.bg-custom{
  background-color:#743eab;
  background-color:var(--main-bg-color);
  /*font-weight: bold;*/
}
.btn-outline-custom{
  border-color:#743eab;
  border-color:var(--main-bg-color);
  background: white;
  color:#743eab;
  color:var(--main-bg-color);
}

.btn-outline-custom:hover{
  border-color:#743eab;
  border-color:var(--main-bg-color);
  background: #743eab;
  background: var(--main-bg-color);
  color:white;
}
span:hover{
  text-decoration:none;
}
.list-group > .list-group-item > .link:hover{
	text-decoration: none;
}
.post-content{
	width: 100%;
}


.cursor-pointer{
	cursor:pointer;
}

.option > button{
	width: 80%;
}
.progress-bar{
	height: 35px;
}
.bg-custom{
  background-color:var(--main-bg-color);
  /*font-weight: bold;*/
}
.text-custom{
	color:var(--main-bg-color);
}
.feedback-page{
    height: 100vh;
}

.feedback-page > .feedback-page-header{
    height: 8%;
    background-color: blue;
}

.feedback-page > .feedback-page-body{
    width: 100%;   
    height: 80%;
    /*background-color: blue;*/
}
.feedback-page > .feedback-page-body > .form-group{
    width: 100%;
    /*background-color: blue;*/
}

.button{
    margin-top:5.1em;
    /*background-color: blue;*/
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
    line-height: 60px;
    position: -webkit-sticky;
    position: sticky;
}
.footer > .container > span >i{
	color: white;
}
.signin-page{
  height:100vh;
}
.signin-page > .header{
  height: 30%;
  background-color: #743eab;

}

.signin-page > .body {
  height:60%;
}

.signin-page > .body {
  width: 100%;
  padding-top: 40px;
  padding-bottom: 20px;
  background-color: #f5f5f5;
}

.signin-page > footer{
  height: 10%;
  background-color: #658e9a;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="username"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

